header {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
}
.header-top {
    background-color: #ecf5fa;
    &__row {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: center;
        justify-content: space-between;
        align-items: center;
        padding: 12px;
    }

    &__item {
        span {
            font-size: 12px;
            font-family: "Open Sans", sans-serif;
            font-weight: 600;
        }
        p {
            font-size: 12px;
            font-family: "Open Sans", sans-serif;
            font-weight: 700;
        }
        .span-r {
            font-weight: 400;
        }
        .span-b {
            font-weight: 700;
        }
        .fz-default {
            font-size: 14px;
        }
    }

    &__column {
        display: inline-block;
        vertical-align: middle;
        max-width: 262px;
    }

    i {
        margin-right: 10px;
        font-size: 27px;
        color: #8fcef4;
    }
    img {
        margin-right: 10px;
    }
    a {
        color: #000;
    }
}
.blue-c {
    color: #174e7f;
}
.header-middle {
    
    &__row {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-end;
        flex-direction: row;
        padding: 20px 0;
    }

    &__item {
    }

    &__logo {
        max-width: 200px;
    }

    &__search {
        margin-bottom: 10px;
    }

    &__form {
        position: relative;
        min-width: 540px;
    }

    &__input {
        padding: 12px 5px;
        outline: none;
    }

    &__input {
        &_text {
            width: 100%;
            background-color: transparent;
            border: 1px solid #c3c3c3;
            padding: 12px 160px 12px 15px;
            font-size: 14px;
            font-weight: 600;
            color: #000;
        }
    }
    input::placeholder {
        font-size: 14px;
        font-weight: 600;
        color: #000;
    }

    &__wrap {
        position: absolute;
        top: 0;
        right: 0;
        padding: 2px 12px;
        cursor: pointer;
        border: 4px solid #ef9c9c;
        background-color: #e03939;
    }

    &__icon {
        color: #fff;
        font-size: 13px;
    }

    &__submit {
        border: none;
        background: none;
        cursor: pointer;
        color: #fff;
        font-style: normal;
        font-weight: 600;
        font-size: 13px;
        padding: 8px 5px;
    }

    &__phone {
        &_title {
            font-size: 16px;
            font-weight: 700;
            margin-bottom: 10px;
        }

        &_link {
            color: #000;
            font-size: 18px;
            font-weight: 300;
            a {
                display: block;
                color: #000;
                &:last-child {
                    margin-top: 5px;
                    img {
                        margin-right: 16px;
                    }
                }
            }
            img {
                vertical-align: middle;
                margin-right: 5px;
            }
        }
    }

    &__btn {
        margin-bottom: 5px;
        button {
            border-radius: 5px;
            background-color: rgb(4, 197, 255);
            box-shadow: 0px 2px 27px 0px rgba(4, 197, 255, 0.3);
            color: #fff;
            font-size: 18px;
            font-weight: 400;
            height: 50px;
            padding: 0 15px;
            border: unset;
            cursor: pointer;
        }
    }
    &__cart {
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 15px;
    }
    &__cart-title {
        display: block;
        font-size: 14px;
        font-weight: 400;
        text-align: left;
    }
    &__cart-count {
        display: block;
        font-size: 16px;
        font-weight: 600;
        text-align: left;
    }
}
.header-bottom {
    &__row {
        border-top: 2px solid #d7d7d7;
        height: 50px;
        background-color: #04c5ff;
        opacity: 0.5;
    }
}

.burger {
    display: none;
    background-color: rgb(17, 142, 213);
    width: 50px;
    height: 50px;
}
