.footer {
    background-color: #fff;
    &__row {
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
        gap: 48px;
        padding: 70px 0;
    }

    &__column {
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        flex: 0 0 22%;
        &:first-child {
            gap: 35px;
        }
    }

    &__logo {
    }

    &__desc {
        font-size: 16px;
        font-weight: 400;
        text-align: left;
    }

    &__social {
    }

    &__social-title {
        font-size: 18px;
        font-weight: 700;
        text-align: left;
        color: #4a4a4a;
    }

    &__social-icon {
        padding-top: 20px;
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
        gap: 15px;
        .cirle {
            display: inline-block;
            position: relative;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            &_grey {
                background-color: #cccbcb;
                transition: linear .4s;
                &:hover {
                    background-color: #118ed5;
                }
            }
            &_vk {
                &:before {
                    position: absolute;
                    content: '';
                    background: url('/img/vk.png') no-repeat;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 25px;
                    height: 15px;
                }
            }
            &_ok {
                &:before {
                    position: absolute;
                    content: '';
                    background: url('/img/ok_ico.png') no-repeat;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 18px;
                    height: 30px;
                }
            }
            &_tw {
                &:before {
                    position: absolute;
                    content: '';
                    background: url('/img/twitter-3_icon-icons.com_50735.png') no-repeat;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 30px;
                    height: 25px;
                }
            }
            &_fb {
                &:before {
                    position: absolute;
                    content: '';
                    background: url('/img/facebook.png') no-repeat;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 15px;
                    height: 30px;
                }
            }
        }
    }

    &__tel {
        > p {
            font-size: 16px;
            font-weight: 700;
            color: #000;
            text-align: left;
            padding-bottom: 8px;
        }
        > a {
            max-width: 180px;
            text-align: right;
            display: block;
            padding: 7px 0;
            font-size: 18px;
            font-weight: 300;
            color: #000;
            img {
                margin-right: 5px;
                vertical-align: middle;
                float: left;
            }
        }
    }

    &__loc {
        display: flex;
        flex-flow: row;
        align-items: center;
        i {
            font-size: 27px;
            color: #8fcef4;
            margin-right: 11px;
            vertical-align: middle;
        }
        > span {
            font-size: 14px;
            font-weight: 400;
            text-align: left;
        }
    }

    &__mail {
        display: flex;
        flex-flow: row;
        align-items: center;
        i {
            font-size: 27px;
            color: #8fcef4;
            margin-right: 11px;
        }
        > a {
            font-size: 14px;
            font-weight: 700;
            text-align: left;
            color: #000;
        }
    }

    &__time {
        display: flex;
        flex-flow: row;
        align-items: center;
        img {
            margin-right: 11px;
        }
        > span {
            font-size: 14px;
            font-weight: 400;
            text-align: left;
        }
    }

    &__widget {
        color: #4a4a4a;
        text-align: left;
        p {
            padding: 10px 0;
        }
    }

    &__widget-title {
        font-size: 18px;
        font-weight: 700;
        padding-bottom: 15px;
        &_apart {
            padding-bottom: 0;
            margin-bottom: 15px;
        }
    }

    &__widget-list {
        ul {
            li {
                font-size: 14px;
                font-weight: 400;
                line-height: 1.333;
                padding-bottom: 10px;
                &:last-child {
                    padding-bottom: 35px;
                }
            }
        }
    }

    &__widget-link {
        a {
            display: block;
            font-size: 14px;
            font-weight: 600;
            color: #118ed5;
            text-decoration: underline;
            margin: 6px 0;
        }
    }

    &__widget-copy {
        font-size: 14px;
        font-weight: 700;
        color: #606060;
        line-height: 1.714;
    }
}
