//fonts
$font-base: 'Inter', sans-serif;

$fw: (
   base: 400,
   bold: 700
);



@function fw($weight) {
  @return map_get($fw, $weight);
}
/***********/

//colors
$body-color: #ffffff;
$services-bg-color: #F8F8F8;


$font-colors: (
        base: #333333,
        button: #ffffff,
        accent: #FE7F2D,
        muted: #767676,
        h1: #333333,
        'white': #ffffff
);

@function font-color($font-name) {
  @return map_get($font-colors, $font-name);
}
/***********/

//buttons
$button-bgs: (
        classic: #102343,
        hover: #102343,
        'white': #FFFFFF,
        white-hover: #F6F6F6
);



@function button-bg($color-name) {
  @return map_get($button-bgs, $color-name);
}

$lh-btn-smallest: 17px;
$lh-btn-default: 23px;
$lh-btn-small: 21px;
/***********/

//sizes
$fz-small: 14px;
$fz-base: 18px;
$fz-big: 18px;
$fz-h1: 64px;
$fz-h2: 32px;
$fz-sub-menu: 22px;

$lh-base: 19px;
$lh-big: 21px;
$lh-large: 24px;
$lh-h2: 38px;
/***********/

//inputs
$input-border: #CDCDCD;
$input-border-hover: #767676;
$input-color: #767676;


/**********/

//spacers
$section-spacer: 104px;

//hover 

$red: linear-gradient(180deg, #EF3054 0%, #D11236 100%);
$orange: linear-gradient(180deg, #FF6F59 0%, #E1513B 100%);
$aqua: linear-gradient(180deg, #28AFB0 0%, #0A9192 100%);
$violet: linear-gradient(180deg, #9590A8 0%, #77728A 100%);


$header-bg: #3f51b5;
$menu-width: 250px!default;