.slider {
    &__row {
        position: relative;
        padding: 10px 0;
    }
    .slide-m-dots {
        position: relative;
        width: max-content;
        margin: auto;
        height: 45px;
        .slick-prev,
        .slick-next {
            width: auto;
            height: auto;
            transform: translateY(-50%);
        }
        .slick-prev {
            left: -40px;
            top: 50%;
        }
        .slick-next {
            right: -40px;
            top: 50%;
        }
        .slick-prev:before,
        .slick-next:before {
            position: relative;
        }
        .slick-prev:before {
            content: url(/img/left-arrow-m.png);
        }
        .slick-next:before {
            content: url(/img/right-arrow-m.png);
        }
        .slick-dots li button:before {
            font-size: 15px;
            line-height: 15px;
            top: 0;
            left: 0;
            width: 15px;
            height: 15px;
            opacity: 1;
            color: #e1e1e1;
        }
        .slick-dots li.slick-active button:before {
            opacity: 1;
            color: #04c5ff;
        }
        .slick-dots li {
            width: 15px;
            height: 15px;
            margin: 0 5px;
        }
        .slick-dots {
            position: relative;
            bottom: unset;
            margin-left: 0;
            top: 50%;
            left: 50%;
            transform: translateY(-50%) translateX(-50%);
        }
    }
    &__item {
    }

    &__box {
        position: relative;
    }

    &__title {
        position: absolute;
        font-weight: 700;
        font-size: 36px;
        text-transform: uppercase;
        left: 0;
        top: 40px;
        z-index: 1;
        span {
            display: block;
            font-size: 48px;
            text-decoration: underline;
        }
        &_wrapper {
            max-width: 570px;
            width: 100%;
            margin-left: 50px;
            margin-right: 50px;
        }
    }

    &__bg {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: flex-start;
        &_white {
            flex: 0 0 30%;
        }

        &_img {
            flex: 0 0 70%;
        }
    }
    &__buttons {
        position: absolute;
        bottom: 175px;
        left: 0;
        button {
            position: relative;
            background-color: #118ed5;
            width: 50px;
            height: 50px;
            border: 1px solid #118ed5;
            cursor: pointer;
            transition: linear 0.4s;
            z-index: 20;
            &:hover {
                background-color: #04c5ff;
                border: 1px solid #fff;
            }
        }
        a {
            position: relative;
            display: inline-block;
            opacity: 0;
            transform: translateX(-55px);
            width: 50px;
            height: 50px;
            background-color: #04c5ff;
            transition: linear 0.2s;
            vertical-align: bottom;
            z-index: 10;
            span {
                display: inline-block;
                height: 50px;
                line-height: 50px;
                padding: 0 18px;
                text-align: center;
                font-size: 16px;
                font-weight: 600;
                color: #fff;
            }
        }
    }

    &__btn {
        button:hover + a,
        button + a:hover,
        button + a:focus {
            transform: translateX(-5px);
            opacity: 1;
            width: auto;
        }
        a + button {
            background-color: #04c5ff;
            border: 1px solid #fff;
        }
    }

    &__calculate {
        position: absolute;
        bottom: 80px;
        left: 0;
        margin: 0 50px;
        form {
            border-radius: 10px;
            background-color: rgb(255, 255, 255);
            box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.1);
            max-width: 570px;
            padding: 30px 25px;
        }
    }

    &__calculate-title {
        font-size: 18px;
        font-weight: 700;
        padding-bottom: 30px;
    }

    &__calculate-input {
        font-size: 18px;
        font-weight: 400;
        color: #939393;
        text-align: center;
        background-color: #eeeeee;
        border: 1px solid #faf9f9;
        border-radius: 5px;
        min-width: 280px;
        height: 50px;
    }
    input::placeholder {
        text-align: center;
        color: #939393;
    }

    &__calculate-submit {
        min-width: 215px;
        height: 50px;
        border-radius: 5px;
        background-color: rgb(224, 57, 57);
        box-shadow: 0px 2px 27px 0px rgba(224, 57, 57, 0.3);
        border: unset;
        border-radius: 5px;
        margin-left: 15px;
        font-size: 18px;
        font-weight: 400;
        color: #fff;
        cursor: pointer;
    }

    &__calculate-pd {
        padding-top: 19px;
        font-size: 14px;
        font-weight: 400;
        a {
            color: #50ace0;
            text-decoration: underline;
        }
    }
    .slick-dots {
        text-align: left;
        padding-left: 50px;
    }
}
