@media screen and (max-width: 1200px) {
    .header-bottom {
        display: none;
        order: 1;
    }
    .header-top {
        display: none;
        order: 2;
    }
    .header-middle {
        order: 3;
        &__row {
            column-gap: 35px;
        }
        &__item {
            &:nth-child(1) {
                order: 1;
            }
            &:nth-child(2) {
                order: 4;
                flex-grow: 10;
            }
            &:nth-child(3) {
                order: 2;
            }
            &:nth-child(4) {
                order: 3;
            }
            &:nth-child(5) {
                order: 5;
                flex-grow: 1;
            }
            &:nth-child(6) {
                order: 6;
            }
        }
        .burger {
            display: block;
        }
    }
    .slider {
        &__title {
            &_wrapper {
                max-width: 700px;
                padding: 0 40px;
            }
        }
        &__bg {
            &_white {
                flex: 0 0 50%;
            }
            &_img {
                flex: 0 0 50%;
                img {
                    min-height: 490px;
                    width: 100%;
                    object-fit: cover;
                    object-position: center;
                }
            }
        }
    }
    .services {
        &__title {
            margin-top: 30px;
        }
        &__row {
            justify-content: center;
            gap: 20px;
        }
        &__item {
            flex: 0 0 35%;
        }
    }
    .cost {
        &__title {
            padding: 70px 0 50px;
        }
        &__column {
            max-width: 100%;
        }
        &__about {
            gap: 120px;
        }
        &__about-left {
            flex: 0 0 100%;
            justify-content: center;
        }
        &__about-right {
            flex: 0 0 100%;

            > p {
                display: inline-block;
                vertical-align: text-top;
            }
        }
        &__about-row {
            display: inline-flex;
            justify-content: flex-end;
            width: 78%;
        }
    }
    .reviews {
        &__item {
            button {
                display: none;
            }
        }
        .slick-slider .slick-prev {
            left: 20px;
            z-index: 1;
        }
        .slick-slider .slick-next {
            right: 20px;
        }
        &__block {
            margin: auto;
            height: 100%;
        }
    }
    .calc {
        &__menu {
            ul {
                flex-direction: column;
                border-bottom: hidden;
                li {
                    justify-content: center;
                    width: 100%;
                }
            }
        }
        #depth-range {
            max-width: 50%;
        }
    }
    div.selectboxss {
        width: 100%;
        position: relative;
        height: 50px;
        border: solid 1px #118ed5;
        background-color: #118ed5;
        color: #fff;
        cursor: pointer;
        overflow: hidden;
        margin-bottom: 30px;
        transition: 0.3s;
        .selectboxssvalue {
            padding: 16px 31px 0px 16px;
            cursor: pointer;
            transition: 0.2s;
            height: 54px;
            margin: 0px;
            display: block;
            position: relative;
        }
        img.arrowselect {
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
            width: 21px;
            height: 7px;
            padding: 0px 10px 0px 0px;
        }
    }
    &::-webkit-scrollbar {
        background: transparent;
        width: 0.5em;
        position: absolute;
    }
    &::-webkit-scrollbar-track {
        background: transparent;
        position: absolute;
        z-index: -2;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 100px;
        background: #118ed5;
    }
    ul.selectboxssmenu {
        background: #118ed5;
        transition: 0.3s;
        width: 100%;
        height: 200px;
        overflow-y: auto;
        overflow-x: hidden !important;
        position: absolute;
        margin-top: 0px;
        display: block;
        padding: 0px;
        margin: 0px;
        > li {
            display: block;
            padding: 10px;
            border-radius: 0px;
            cursor: pointer;
        }
        > li.selectoption {
            color: #fff;
            font-size: 16px;
            padding: 10px 16px;
            height: auto;
            border-bottom: 2px solid #fff;
            &:hover {
                color: #333;
                background: #118ed5;
                transition: 0.2s;
            }
        }
    }
    .question-form {
        &__inputblock {
            flex-direction: column;
            gap: 20px;
        }
        &__input {
            max-width: 100%;
        }
    }
    .photo-slider {
        &__item {
            button {
                display: none;
            }
        }
        .slick-slider .slick-prev {
            left: 20px;
            z-index: 1;
        }
        .slick-slider .slick-next {
            right: 20px;
        }
    }
    .last-job {
        &__cards {
            justify-content: center;
            gap: 14px;
        }
        &__item {
            max-width: 360px;
        }
        &__img {
            width: 100%;
            img {
                width: 100%;
            }
        }
    }
    .licence {
        &__column {
            display: block;
        }
        &__item {
            max-width: unset;
            img {
                max-width: 360px;
                width: 100%;
                margin: auto;
            }
        }
    }
    .blog {
        &__cards {
            justify-content: center;
        }
    }
    .footer {
        &__row {
            justify-content: center;
        }
        &__column {
            flex: 0 0 32%;
        }
    }
    .slick-slider .slick-next {
        right: 20px;
    }
    .slick-slider .slick-prev {
        left: 20px;
        z-index: 1;
    }
}
@media screen and (max-width: 992px) {
    .blog {
        &__item {
            max-width: 100%;
        }
    }
}
@media screen and (max-width: 800px) {
    .header-middle {
        &__row {
            column-gap: unset;
        }
        &__form {
            position: relative;
            min-width: 440px;
        }
    }
}
@media screen and (max-width: 768px) {
    .header-middle {
        &__row {
            gap: 20px;
            align-items: center;
        }
        &__item {
            &:nth-child(1) {
                order: 1;
                flex: 1 0 0;
            }
            &:nth-child(2) {
                order: 2;
                flex: 10 0 30%;
            }
            &:nth-child(3) {
                display: none;
            }
            &:nth-child(4) {
                order: 3;
                flex: 1 0 35%;
                text-align: end;
            }
            &:nth-child(5) {
                order: 5;
                flex: 0 0 48%;
            }
            &:nth-child(6) {
                order: 6;
                flex: 0 0 47%;
                text-align: end;
            }
        }
        &__cart {
            margin-bottom: 0;
        }
        &__logo {
            max-width: 100%;
        }
        &__phone {
            &_link {
                font-size: 16px;
            }
        }
        .burger {
            display: block;
        }
    }
    .header-middle__item:nth-child(5) .header-middle__phone_title {
        display: none;
    }
    .photo-slider {
        &__item {
            opacity: 1;
        }
        &__img {
            width: 100%;
            img {
                margin: auto;
            }
            &_two {
                display: none;
            }
        }
    }
    .slider {
        .container {
            padding: 0;
        }
        &__title {
            &_wrapper {
                padding: 0 20px;
                margin: 0;
            }
            font-size: 26px;
            span {
                font-size: 32px;
            }
        }
        &__bg {
            &_white {
                flex: 0 0 0;
            }
            &_img {
                flex: 0 0 100%;
                img {
                }
            }
        }
        &__box {
            button {
                display: none;
            }
        }
        .slick-slider .slick-prev {
            left: 20px;
            top: 100%;
            z-index: 1;
        }
        .slick-slider .slick-next {
            right: 20px;
            top: 100%;
            z-index: 1;
        }
        .slick-dots {
            max-width: max-content;
        }
        &__calculate {
            top: 12rem;
            bottom: unset;
            margin: 0 20px;
            form {
                padding: 30px 75px;
            }
        }
        &__calculate-title {
            text-align: center;
        }
        &__calculate-input {
            width: 100%;
            margin-bottom: 10px;
        }
        &__calculate-submit {
            width: 100%;
            margin-left: 0;
        }
        &__buttons {
            top: 17.2rem;
            left: 10px;
            bottom: unset;
            margin: 0 20px;
        }
        &__btn {
            margin: 15px 0;
        }
    }
    .services {
        &__title {
            text-align: center;
        }
        &__item {
            flex: 0 0 100%;
        }
        &__card {
            margin: auto;
        }
    }
    .cost {
        &__row,
        &__about {
            display: block;
        }
        &__title {
            font-size: 30px;
            text-align: center;
        }
        &__column {
            min-height: 100%;
            padding: 30px 30px 110px;
        }

        &__about-left {
            gap: 100px;
            margin-bottom: 35px;
        }
        &__img {
            margin: auto;
            max-height: max-content;
            box-shadow: unset;
            img {
                height: auto;
                margin: auto;
            }
            &:hover {
                background-color: transparent;
                img {
                    opacity: 1;
                }
            }
            &:hover::before {
                content: unset;
            }
        }
        .slick-dotted.slick-slider {
            margin-bottom: 50px;
        }
    }
    .reviews {
        &__title {
            font-size: 30px;
        }
        &__img {
            display: none;
        }
        &__item {
            opacity: 1;
            padding: 0 20px;
        }
        &__block {
            max-width: 480px;
        }
    }
    .calc {
        &__column {
            padding: 60px 0;
        }
        &__title {
            font-size: 30px;
            padding-bottom: 40px;
        }
        .select-box {
            margin-top: 20px;
        }
        #depth-range {
            margin-top: 20px;
            max-width: 100%;
            margin-left: 0;
            margin-right: -55px;
        }
        label[for="depth-range"] {
            position: relative;
        }
        label[for="depth-range"] > span {
            position: relative;
            top: -20px;
        }
        .mobile-flex {
            display: flex;
            flex-flow: row wrap;
            justify-content: flex-start;
            align-items: flex-start;
        }
        .mobile-form-title {
            display: block;
            width: 100%;
        }
        &__check {
            flex: 0 0 20%;
            margin: 17px 0;
            &:first-child {
                margin-left: 0;
            }
            > label {
                font-size: 16px;
            }
        }
    }
    .question {
        &__column {
            padding: 60px 0;
        }
        &__title {
            font-size: 30px;
        }
        &__acc {
            padding: 30px 0 0;
        }
        &__acc-title {
            background: #fff url(/img/icons8-minus-50.png) 95% center no-repeat;
        }
        .question__acc-title.ui-state-hover,
        .question__acc-title.ui-state-active {
            background: #fff url(/img/icons8-plus-50ui.png) 95% center no-repeat;
        }
    }
    .question-form {
        &__title {
            font-size: 30px;
            line-height: 1.2;
        }
        &__subtitle {
            padding-top: 30px;
            font-size: 18px;
            line-height: 1.2;
        }
        &__pd {
            margin-top: 15px;
            margin-left: 0;
        }
    }
    .photo-slider {
        &__column {
            padding: 60px 0;
        }
        &__title {
            font-size: 24px;
        }
    }
    .map {
        &__column {
            padding: 60px 0;
        }
        &__title {
            font-size: 30px;
        }
    }
    .last-job {
        &__column {
            padding: 60px 0;
            align-items: center;
        }
        &__title {
            font-size: 30px;
            text-align: center;
        }
    }
    .licence {
        &__column {
            padding: 60px 0;
        }
        &__title {
            font-size: 30px;
        }
    }
    .blog {
        &__column {
            padding: 60px 0;
        }
        &__title {
            font-size: 30px;
        }
    }
    .seo-text {
        &__column {
            padding: 60px 0;
        }
        &__title {
            font-size: 36px;
            margin-bottom: 35px;
        }
    }
}
@media screen and (max-width: 567px) {
    .cost {
        &__about-row {
            width: auto;
            justify-content: space-between;
        }
        &__about-item-text {
            font-size: 30px;
            height: 30px;
            margin: auto;
        }
        &__about-item-icon {
            margin: auto;
        }
        &__about-item-subtext {
            font-size: 14px;
            text-align: center;
            > p {
                font-size: 16px;
            }
        }
    }
    .footer {
        &__column {
            flex: 0 0 100%;
        }
    }
}
@media screen and (max-width: 480px) {
    .cost {
        &__price {
            font-size: 14px;
            span {
                font-size: 18px;
            }
        }
        &__btn {
            button {
                min-width: 100%;
                padding: 0 15px;
            }
        }
    }
    .services {
        &__card {
            margin: auto;
            max-width: 100%;
        }
    }
    .slider {
        &__buttons {
            margin: 0 0;
        }
        &__btn {
            margin: 15px 0;
        }
    }
    .slider__calculate {
        margin: 0;
    }
    .slider__calculate-input {
        min-width: 100%;
    }
    .slider__calculate form {
        max-width: 100%;
    }
    .slider__title span {
        font-size: 25px;
    }
    .slider__title_wrapper {
        font-size: 20px;
    }
    .slider__calculate form {
        padding: 30px 20px 30px 75px;
    }
    .header-middle__phone_link {
        font-size: 12px;
    }
    .header-middle__btn button {
        font-size: 12px;
    }
    .header-middle {
        &__item {
            &:nth-child(5) {
                order: 5;
                flex: 0 0 48%;
            }
            &:nth-child(6) {
                order: 6;
                flex: 0 0 45%;
                text-align: end;
            }
        }
    }
    .reviews {
        &__block {
            max-width: 300px;
        }
    }
    .calc {
        &__block {
            padding: 30px 20px;
        }
        &__check {
            flex: 0 0 100%;
        }
        &__result {
            max-width: 100px;
            margin-left: 15px;
            .calc__price {
                font-size: 20px;
            }
            span {
                font-size: 16px;
            }
        }
    }
    .blog {
        &__name {
            top: 30%;
            font-size: 14px;
        }
    }
}
@media screen and (max-width: 320px) {
}
